import React, { useState } from 'react';
import * as FaIcons from 'react-icons/fa';
import * as AiIcons from 'react-icons/ai';
import { Link } from 'react-router-dom';
import { SidebarItem } from './SidebarItem';
import './Navbar.css';
import { IconContext } from 'react-icons';

// function Navbar() {
    const Navbar=(props)=>{
        const {hadnlelogout}=props;
  const [sidebar, setSidebar] = useState(false);

  const showSidebar = () => setSidebar(!sidebar);

  return (
    <>
      <IconContext.Provider value={{ color: '#fff' }}>
        <div className='navbar'>
          <Link to='#' className='menu-bars'>
            <FaIcons.FaBars onClick={showSidebar} />
              
          </Link>
          <div>
          <img style={{height:80, width:250}} src="/images/logo3.png" alt="logo"/>
              
          </div>
          <div>
              <button type="button" className="buttonstyle" onClick={hadnlelogout}> Logout</button>
          </div>
        </div>
        <nav className={sidebar ? 'nav-menu active' : 'nav-menu'}>
         
          <ul className='nav-menu-items' onClick={showSidebar}>
          <img className="headerlogo" src="/images/logo3.png" alt="logo"/>
            <li className='navbar-toggle'>
              <Link to='#' className='menu-bars'>
                <AiIcons.AiOutlineClose />
              </Link>
            </li>
            {SidebarItem.map((item, index) => {
              return (
                <li key={index} className={item.cName}>
                  <Link to={item.path}>
                    {item.icon}
                    <span>{item.title}</span>
                  </Link>
                </li>
              );
            })}
          </ul>
        </nav>
      </IconContext.Provider>
    </>
  );
}

export default Navbar;
